<template>
  <div class="weather-container">
    <div class="setting" :class="{ 'display-form' : displaySubmit }">
      <div class="selector">Location:

        <select
          v-show="cities.length > 0"
          v-model="woeid"
          @change="changeLocation()">
          <option
            v-for="city in cities"
            :key="city.woeid"
            :value="city.woeid">
            {{`${city.name}, ${city.country}`}}
          </option>
          <option
            :value="'add-city'">
            Add city
          </option>
        </select>
      </div>
      <form>
        <input
                id="search-weather"
          v-show="displaySubmit"
          v-model="cityName"
          class="input-city search-field"
          placeholder="City, Country">
        <button
          v-show="displaySubmit"
          type="button"
          class="submit-city"
          @click="search()">Add City
        </button>
      </form>
    </div>

    <div
      v-if="!error"
      class="display"
      v-show="!displaySubmit">

      <div class="left-information">
        <div v-if="location">
          <img
            :src="getThumbnail(status, 64)"
            :alt="status"
            class="thumbnail">
          <div class="temperature">{{ temperature }}</div>
          <div class="unit">°C</div>
        </div>
      </div>

      <div class="forecast">
        <ul>
          <li
            class="day-to-day-forecast"
            v-for="(f, i) in forecast.slice(1)"
            v-if="i < 4"
            :key="i">
            <div>{{ f.day }}</div>
            <img
              :src="getThumbnail(f.text, 48)"
              :alt="f.text">
            <div class="high-low-temp">
              <span class="low">{{ f.low }}°</span> - <span class="high">{{ f.high }}°</span>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <div v-else="">Location ID error</div>

    <keyboard
            :text.sync="cityName"
            :input="'search-weather'"/>

  </div>
</template>

<script>
/* global CacheManager */

import axios            from 'axios'
// import { CacheManager } from 'reflectos-sdk'
// import keyboard from './Keyboard'

export default {
  name: 'ur-weather',
  components: {
    // keyboard
  },
  data () {
    return {
      woeid: '',
      location: '',
      status: '',
      time: '',
      temperature: '0',
      humidity: '0',
      wind: '0',
      pressure: '0',
      forecast: [],
      error: false,
      latnLong: {},
      displaySubmit: false,
      cityName: '',
      countryName: '',
      newWoeid: '',
      cities: [],
      cacheM: null
    }
  },
  mounted () {
    try {
      this.cacheM = new CacheManager('ur-weather')
      this.cacheM.store('weather', 'weather')
      if (this.cacheM.get('cities')) {
        this.cities = JSON.parse(this.cacheM.get('cities'))
        this.woeid = this.cities[0].woeid
      }
    } catch (e) {
      if (e) throw e
    }
    if (this.cities.length === 0) {
      this.displaySubmit = true
    }
    if (this.woeid.length > 0) {
      this.changeLocation()
    }
  },
  methods: {
    search () {
      let place = {}
      if (this.cityName !== '') {
        axios.get(`https://query.yahooapis.com/v1/public/yql?q=select%20*%20from%20geo.places%20where%20text%20%3D%20%22${encodeURIComponent(this.cityName)}%22&format=json`).then(response => {
          if (response.data.query.results.place.woeid) {
            place = response.data.query.results.place
          } else {
            place = response.data.query.results.place[0]
          }
          if (this.cities.length > 0) {
            for (const city of this.cities) {
              if (city.woeid === place.woeid) {
                this.displaySubmit = false
                return
              }
            }
          }
          this.cities.push({
            woeid: place.woeid,
            name: place.name,
            country: place.country.content
          })
          this.cacheM.store('cities', JSON.stringify(this.cities))
          this.woeid = place.woeid
          this.changeLocation()
          this.displaySubmit = false
          this.cityName = ''
        }).catch(e => {
          if (e) throw e
        })
      }
    },
    changeLocation: function () {
      if (this.woeid !== 'add-city') {
        this.displaySubmit = false
        axios.get(`https://query.yahooapis.com/v1/public/yql?q=select%20*%20from%20weather.forecast%20where%20woeid%20%3D%20${this.woeid}%20and%20u%20%3D%20'c'&format=json&env=store%3A%2F%2Fdatatables.org%2Falltableswithkeys`).then(response => {
          const channel = response.data.query.results.channel
          if (channel) {
            this.location = channel.location.city
            this.countryName = channel.location.country
            this.time = channel.item.pubdate
            this.status = channel.item.condition.text
            this.temperature = channel.item.condition.temp
            this.forecast = channel.item.forecast
          } else {
            this.error = true
          }
        }).catch(e => {
          this.error = true
          if (e) throw e
        })
      } else {
        this.displaySubmit = true
      }
    },
    getThumbnail: function (status, size) {
      switch (status.toLowerCase()) {
        case 'hot':
          return `https://ssl.gstatic.com/onebox/weather/${size}/hot.png`
        case 'sunny':
        case 'mostly sunny':
          return `https://ssl.gstatic.com/onebox/weather/${size}/sunny.png`
        case 'thunderstorms':
        case 'severe thunderstorms':
          return `https://ssl.gstatic.com/onebox/weather/${size}/thunderstorms.png`
        case 'scattered thunderstorms':
          return `https://ssl.gstatic.com/onebox/weather/${size}/rain_s_cloudy.png`
        case 'partly cloudy':
        case 'mostly cloudy':
          return `https://ssl.gstatic.com/onebox/weather/${size}/partly_cloudy.png`
        case 'cloudy':
          return `https://ssl.gstatic.com/onebox/weather/${size}/cloudy.png`
        case 'showers':
        case 'scattered showers':
          return `https://ssl.gstatic.com/onebox/weather/${size}/rain_light.png`
        case 'rain':
          return `https://ssl.gstatic.com/onebox/weather/${size}/rain.png`
        case 'snow':
        case 'heavy snow':
        case 'snow flurries':
        case 'blowing snow':
          return `https://ssl.gstatic.com/onebox/weather/${size}/snow.png`
        case 'sleet':
          return `https://ssl.gstatic.com/onebox/weather/${size}/sleet.png`
        case 'windy':
          return `https://ssl.gstatic.com/onebox/weather/${size}/windy.png`
        default:
          return `https://ssl.gstatic.com/onebox/weather/${size}/cloudy.png`
      }
    }
  }
}
</script>

